import React from "react"
import "./About.css"
import Img from "gatsby-image"
import useAvatar from "../../hooks/use-avatar"
import { FaFacebookSquare, FaLinkedinIn } from "react-icons/fa"
import {
  AiFillCodeSandboxCircle,
  AiFillMediumCircle,
  AiOutlineGithub,
} from "react-icons/ai"
import { MdEmail } from "react-icons/md"
import { DownloadOutlined } from "@ant-design/icons"
import { Typography, Row, Col, Button, Tooltip, Divider } from "antd"

const { Title, Paragraph } = Typography
const About = () => {
  const image = useAvatar(64, 64)
  //Render
  return (
    <React.Fragment>
      <Img
        fixed={image.childImageSharp.fixed}
        style={{
          marginBottom: "16px",
          borderRadius: "50%",
          height: "80px",
          width: "80px",
        }}
        alt="Avatar"
      ></Img>
      <Title level={4} className="title" type="primary">
        Hieu Le
      </Title>
      <Paragraph className="about-text">
        I am a developer who highly interested in web development. Starting with
        front-end development, I have been exploring back-end technologies such
        as NodeJS, Java Spring Boot. I enjoy writing blogs as a note for myself
        and sharing my knowledge.
      </Paragraph>
      <Divider dashed="true"></Divider>
      <Row type="flex" justify="start" className="icon-btn-group">
        <Col md={4} lg={8}>
          <Tooltip title="Github">
            <Button
              href="https://github.com/hieuvanle"
              shape="circle"
              icon={<AiOutlineGithub className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
        <Col md={4} lg={8}>
          <Tooltip title="Linkedin">
            <Button
              href="https://www.linkedin.com/in/hle/"
              shape="circle"
              icon={<FaLinkedinIn className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
        <Col md={4} lg={8}>
          <Tooltip title="Leetcode">
            <Button
              href="https://leetcode.com/le009660/"
              shape="circle"
              icon={<AiFillCodeSandboxCircle className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
        <Col md={4} lg={8}>
          <Tooltip title="Medium">
            <Button
              disabled="true"
              shape="circle"
              icon={<AiFillMediumCircle className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
        <Col md={4} lg={8}>
          <Tooltip title="Email">
            <Button
              href="mailto:hieu.le@stthomas.edu"
              shape="circle"
              icon={<MdEmail className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
        <Col md={4} lg={8}>
          <Tooltip title="Facebook">
            <Button
              disabled="true"
              shape="circle"
              icon={<FaFacebookSquare className="icon" />}
            ></Button>
          </Tooltip>
        </Col>
      </Row>
      <div className="btn-group">
        <Button
          className="action-btn"
          type="primary"
          icon={<DownloadOutlined />}
        >
          My CV
        </Button>
        <Button className="action-btn" href="mailto:hieu.le@stthomas.edu">
          Email me
        </Button>
      </div>
      <Divider dashed="true"></Divider>
      <div className="footer">
        <p>@ {new Date().getFullYear()} Hieu Le. All rights reserved.</p>
        <p>
          Built with{" "}
          <a
            href="https://ant.design/"
            target="_blank"
            rel="noopener noreferrer"
          >
            Ant Design{" "}
          </a>
          and{" "}
          <a
            href="https://www.gatsbyjs.org/"
            target="_blank"
            rel="noopener noreferrer"
          >
            Gatsby
          </a>
        </p>
      </div>
    </React.Fragment>
  )
}

export default About
